'use client';

import { Modal, Text, Image, Group, Badge, Tabs, SimpleGrid, ThemeIcon, List, ScrollArea } from '@mantine/core';
import { IconPhoto, IconListDetails, IconDownload, IconMicrophone, IconVideo, IconTools, IconAlertTriangle } from '@tabler/icons-react';

export function FacilityModal({ facility, onClose }) {
    if (!facility) return null;

    return (
        <Modal
            opened={!!facility}
            onClose={onClose}
            title={<Text fw={700} size="xl">{facility.name}</Text>}
            size="xl"
            radius="lg"
            padding="xl"
            centered
            scrollAreaComponent={ScrollArea.Autosize}
        >
            <Image
                src={facility.image}
                height={300}
                radius="md"
                alt={facility.name}
                fallbackSrc="https://placehold.co/600x400?text=No+Image"
                mb="xl"
            />

            <Tabs defaultValue="info" color="blue">
                <Tabs.List mb="md">
                    <Tabs.Tab value="info" leftSection={<IconListDetails size={16} />}>
                        기본 정보
                    </Tabs.Tab>
                    {facility.subItems && (
                        <>
                            <Tabs.Tab value="gallery" leftSection={<IconPhoto size={16} />}>
                                갤러리
                            </Tabs.Tab>
                            <Tabs.Tab value="specs" leftSection={<IconTools size={16} />}>
                                상세 제원
                            </Tabs.Tab>
                        </>
                    )}
                </Tabs.List>

                <Tabs.Panel value="info">
                    <Text size="lg" mb="md">{facility.description}</Text>

                    <Group gap="xl" mb="xl">
                        <div>
                            <Text size="sm" c="dimmed">위치</Text>
                            <Text fw={500}>{facility.location}</Text>
                        </div>
                        <div>
                            <Text size="sm" c="dimmed">수용 인원</Text>
                            <Text fw={500}>{facility.capacity}</Text>
                        </div>
                    </Group>

                    <Text fw={600} mb="sm">보유 장비</Text>
                    <Group gap="xs">
                        {facility.equipment?.map((item, index) => (
                            <Badge key={index} size="lg" variant="dot">{item}</Badge>
                        ))}
                    </Group>
                </Tabs.Panel>

                {facility.subItems && (
                    <>
                        <Tabs.Panel value="gallery">
                            <SimpleGrid cols={2} spacing="md">
                                {facility.images?.map((img, idx) => (
                                    <Image key={idx} src={img} radius="md" />
                                ))}
                                {facility.subItems.find(item => item.type === 'image-gallery')?.images.map((img, idx) => (
                                    <div key={`sub-${idx}`}>
                                        <Image src={img.url} radius="md" />
                                        <Text size="xs" ta="center" mt={4} c="dimmed">{img.caption}</Text>
                                    </div>
                                ))}
                            </SimpleGrid>
                        </Tabs.Panel>

                        <Tabs.Panel value="specs">
                            {facility.subItems.filter(item => item.type === 'rich-content').map((section, idx) => (
                                <div key={idx} style={{ marginBottom: '2rem' }}>
                                    <Text fw={700} size="lg" mb="md">{section.title}</Text>
                                    {section.sections.map((subSec, sIdx) => (
                                        <div key={sIdx} style={{ marginBottom: '1rem', padding: '1rem', backgroundColor: '#f8f9fa', borderRadius: '8px', borderLeft: `4px solid ${subSec.color || '#ccc'}` }}>
                                            <Text fw={600} mb="xs" c={subSec.color}>{subSec.title}</Text>
                                            <List size="sm" spacing="xs" icon={<ThemeIcon color={subSec.color} size={6} radius="xl" variant="filled" />}>
                                                {subSec.items.map((item, iIdx) => (
                                                    <List.Item key={iIdx}>{item}</List.Item>
                                                ))}
                                            </List>
                                        </div>
                                    ))}
                                </div>
                            ))}
                        </Tabs.Panel>
                    </>
                )}
            </Tabs>
        </Modal>
    );
}
